.design {
    background: url('/img/bgDesign.jpg') center center no-repeat;
    background-image: cover;
    position: relative;

    &::before {
        content: '';
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(255, 255, 255, .8);
    }
}

.design__heading {
    position: relative;
    padding-top: 1%;
}

.design__photos {
    padding-top: 0;
    position: relative;
    padding-left: 17.5%;
}

.design__main {
    position: relative;
}

.design__tablet {
    margin: 0;
    padding: 0;
    width: 55.5%;
}

.design__tablet>img {
    max-width: 100%;
    display: block;
}

.design__smartphone {

    position: absolute;
    bottom: -7%;
    left: 55.2%;
    width: 14.5%;
}

.design__smartphone>img {
    max-width: 100%;
}

@media (max-width:1200px) {
    .design__smartphone {
        bottom: -5%;
    }
}